import { useState } from 'react';
import './App.css';


const firstdaylist = [{
    num : 1,
    name : "이호테우해수욕장",
    time : "PM 5:00 ~ PM 5:30 ",
    pointer : ["1.2vw", "2.5vh"],
    info : "입장료 무료"
    },{
    num : 2,
    name : "도두봉",
    time : "PM 5:30 ~ PM 6:00",
    pointer : ["1.2vw", "2.5vh"],
    info : "입장료 무료. 주차 공간이 많지 않다고 함"
    },{
    num : 3,
    name : "무지개해안도로",
    time : "PM 6:00 ~ PM 6:30",
    pointer : ["1.2vw", "2.5vh"],
    info : "그냥 도로. 차라리 걸어 다니는 게 나을 수도 있음!"
    },{
    num : 4,
    name : "바이제주",
    time : "PM 7:30 ~ PM 8:00",
    pointer : ["1.2vw", "2.5vh"],
    info : "기념품 Shop",
    },{
    num : 5,
    name : "용연계곡",
    time : "PM 7:30 ~ PM 8:00",
    pointer : ["1.2vw", "2.5vh"],
    info : "Free",
    },{
    num : 6,
    name : "드라이브",
    time : "PM 8:00 ~ PM 9:00",
    pointer : ["1.2vw", "2.5vh"],
    info : "Just feel this moment",
    },{
    num : 7,
    name : "제주 올레길 걷기",
    time : "Anytime",
    pointer : ["1.2vw", "2.5vh"],
    info : "Free",
    }
]



function todolist(){
    return(
        <ul className="list">
            {firstdaylist.map(list => (
                <li className="list_li" >
                    <ul className="list_ul" onClick={(e)=>e.target.classList.toggle("active")}>
                        <li key={list.num} className="list_num">{list.num}<div className="list_name">{list.name}</div></li>
                        <li key={list.time} className="list_time">일정 : {list.time}</li>
                        <li key={list.pointer} className="list_pointer">{list.pointer}</li>
                        <li key={list.info} className="list_info">정보 : {list.info}</li>
                    </ul>
                </li>
            ))}
        </ul>
    )
}

export default todolist;